@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Thai:wght@400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "IBM Plex Sans Thai", sans-serif;
}

.scroll {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;
  height: 5px;
  width: 100%;
}

.scroll .scrollProgress {
  height: 5px;
  background: linear-gradient(139deg, #fb8817 0%, #ff4b01 19%, #c12127 54%, #e02aff 100%);
}

.homeSection {
  width: 100%;
  height: 100vh;
  background-color: #000;
  overflow: hidden;
}

.homeSection .links {
  position: absolute;
  top: 25%;
  left: 20%;
  display: flex;
  flex-direction: column;
}

.homeSection .links .box {
  position: relative;
}

.homeSection .links .box ul li {
  list-style: none;
  margin: 10px 0;
}

.homeSection .links .box ul li span,
.homeSection .links .box ul li span > a {
  text-decoration: none;
  color: #fff;
  font-size: 96px;
  font-weight: 700;
  line-height: 95px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.homeSection .links .box ul li span:last-child {
  opacity: 0;
  position: absolute;
  left: 0;
}

.homeSection .links .box ul li:hover span:first-child {
  opacity: 0;
}

.homeSection .links .box ul li:hover span:last-child {
  left: 30px;
  opacity: 1;
}

.homeSection .links .box ul .link {
  color: red;
}

.homeSection .bgMan {
  background-image: url("https://www.dejan.works/img/DejanHome.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: fixed;
  top: 0;
  left: 40%;
  right: 0;
  bottom: 0;
}

.aboutSection {
  width: 100%;
  background-color: #000;
  color: #fff;
  position: relative;
}

.aboutSection .aboutSec .content {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 54px;
}

.aboutSection .aboutSec .content .skill {
  max-width: 1000px;
  margin: 0 auto;
}

.aboutSection .aboutSec .myPic {
  width: 100%;
}

.aboutSection .aboutSec .myPic .hero img {
  width: 80%;
}

.aboutSection .aboutSec .about {
  padding-bottom: 4rem;
}

.aboutSection .aboutSec .about .info {
  max-width: 700px;
  margin: 0 auto;
}

.aboutSection .aboutSec .about .info h4 {
  padding: 5rem 0 2rem 0;
  font-size: 40px;
  font-weight: 700;
  line-height: 95px;
}

.aboutSection .aboutSec .about .info .large {
  line-height: 160%;
  font-size: 24px;
  margin-bottom: 2rem;
  font-weight: 400;
}

.aboutSection .aboutSec .about .info p {
  line-height: 170%;
  margin-bottom: 2rem;
  font-size: 22px;
  position: relative;
}

.aboutSection .aboutSec .about .info p a {
  text-decoration: none;
  color: red;
  line-height: 170%;
  font-size: 22px;
  font-weight: 500;
  transition: transform 0.3s ease;
  position: absolute;
}

.aboutSection .aboutSec .about .info p a:hover {
  transform: translateX(10%);
}

.aboutSection .aboutSec .about .info p a:active {
  transform: translateX(0);
}

.aboutSection .aboutSec .about .info p:last-child {
  margin-bottom: 0;
}

.workSection {
  background-color: #000;
  padding-bottom: 15vh;
  color: #fff;
}

.workSection .workSec {
  max-width: 1000px;
  margin: 0 auto;
}

.workSection .workSec .myWorks {
  padding: 25vh 0 5vh 0;
}

.workSection .workSec .myWorks a {
  text-decoration: none;
  color: #fff;
}

.workSection .workSec .myWorks a .work {
  margin: 4rem 0;
  transition: all 0.3s ease;
}

.workSection .workSec .myWorks a .work h2 {
  font-size: 96px;
  font-weight: 700;
  line-height: 135px;
}

.workSection .workSec .myWorks a .work p {
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
}

.workSection .workSec .myWorks a .work p:nth-child(2) {
  color: red;
}

.workSection .workSec .myWorks a .work p:last-child {
  color: #929292;
}

.workSection .workSec .myWorks a .work:hover {
  transform: translateX(3%);
}

.contactSection {
  background-color: #000;
}

.contactSection .contactSec {
  max-width: 800px;
  margin: 0 auto;
}

.contactSection .contactSec .contactText {
  padding: 30vh 0 5vh 0;
  height: 100vh;
}

.contactSection .contactSec .contactText h2 {
  font-size: 96px;
  font-weight: 700;
  line-height: 135px;
  color: #fff;
}

.contactSection .contactSec .contactContent {
  color: #fff;
}

.contactSection .contactSec .contactContent h4 {
  font-size: 40px;
  font-weight: 700;
  line-height: 95px;
}

.contactSection .contactSec .contactContent p {
  font-size: 24px;
  font-weight: 400;
  line-height: 27px;
  margin-bottom: 2rem;
}

.contactSection .contactSec .contactContent .contactLinks {
  padding-bottom: 5rem;
}

.contactSection .contactSec .contactContent .contactLinks a {
  display: block;
  text-decoration: none;
  color: red;
  font-size: 24px;
  font-weight: 400;
  line-height: 40px;
  transition: padding 0.3s ease;
}

.contactSection .contactSec .contactContent .contactLinks a:hover {
  padding-left: 1%;
}

.contactSection .contactSec .contactContent .contactLinks a:active {
  padding: 0;
}

.back {
  position: fixed;
  top: 15%;
  right: 15%;
  z-index: 1;
}

.back .backToHome {
  display: flex;
  text-decoration: none;
  color: red;
}

.back .backToHome p {
  transition: all 0.3s ease;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
}

.back .backToHome p:first-child::before {
  content: "<";
}

.back .backToHome p:last-child {
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.back .backToHome p:first-child:hover {
  opacity: 0;
}

.back .backToHome p:first-child:hover ~ p:last-child {
  opacity: 1;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

::selection {
  color: red;
  background: transparent;
}

@media (max-width: 575.98px) {
  .homeSection .links {
    z-index: 99;
  }
  .homeSection .links .box ul li span,
  .homeSection .links .box ul li span > a {
    font-size: 48px;
    line-height: 68px;
  }
  .homeSection .bgMan {
    background-size: cover;
    left: 0;
    opacity: 0.5;
  }
  .aboutSection .aboutSec .content .skill h2 {
    font-size: 48px;
    line-height: 68px;
  }
  .aboutSection .aboutSec .myPic .hero img {
    width: 100%;
  }
  .aboutSection .aboutSec .about {
    padding: 0 10px 4rem 10px;
  }
  .workSection .workSec .myWorks a .work {
    margin: 3rem 0;
    padding: 0 15px;
  }
  .workSection .workSec .myWorks a .work h2 {
    font-size: 42px;
    font-weight: 700;
    line-height: 48px;
  }
  .workSection .workSec .myWorks a .work:hover {
    transform: translateX(0);
  }
  .contactSection .contactSec .contactText {
    padding: 30vh 15px 5vh 15px;
  }
  .contactSection .contactSec .contactText h2 {
    font-size: 52px;
    line-height: 68px;
  }
  .contactSection .contactSec .contactContent {
    padding: 0 15px;
  }
}

